@import "styles/variables.scss";

.product {
    display: flex;
    flex-wrap: wrap;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    border-left: 1px solid rgba(35, 35, 35, 0.3);
}

.borderTop > div {
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

@media #{$tablet} {
    .product {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}