@import "styles/variables.scss";

.sizeList > p {
    margin-bottom: 8px;
}

.sizeListCol {
    position: relative;
    z-index: 10;
}

.sizes {
    display: flex;
    justify-content: space-between;
}

.transp_img {
border: 0;
}

.sizes > div:first-child {
    display: flex;
    flex-wrap: wrap;
    max-width: 335px;
}

.sizes > div:first-child a {
    margin-right: 4px;
    text-decoration: none;
}

.size {
    width: fit-content;
    min-width: 30px;
    height: 40px;
    padding: 0 5px;
    margin-bottom: 12px;
    text-align: center;
    line-height: 40px;
    vertical-align: middle;
    font-size: 16px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    cursor: pointer;
}

.sizeBtn {
    background-color: $beige;
    border-bottom: 1px solid #1B2530;
    cursor: pointer;
    height: fit-content;
}

.sizeLabel {
    padding: 12px 16px;
    background-color: $whiteColor;
}

.sizeLabel > p {
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
}

.sizeInfo {
    margin-bottom: 40px;
}

.sizeInfo > p:first-child {
    font-size: 16px;
    margin-bottom: 17px;
}
.sizeListVal,
.columns {
    display: flex;
    margin-bottom: 16px;
}

.sizeListVal > p,
.columns > p {
    font-size: 12px;
    width: 80px;
}

@media #{$tablet} {
    .sizes {
        align-items: flex-end;
        margin-bottom: 16px;
    }
    .size  {
        margin-bottom: 0;
    }
    .sizeListVal > p,
    .columns > p {
        font-size: 12px!important;
    }
}