@import "styles/variables.scss";

.content {
    padding-bottom: 0;
}

.cardCol {
    position: relative;
    z-index: 100000;
}

.card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    position: relative;
}

.productInfo {
    position: sticky;
    top: 100px;
    height: fit-content;
    margin-top: 40px;
    max-width: 500px;
    background-color: $beige;
    z-index: 1000;
    box-sizing: border-box;
}

.buy_on {
    display: flex;
    width: 65%;
    flex-direction: column;
    gap: 8px;
}

.buttons_mt {
    display: flex;
    gap: 8px;
}

.buttons_mt>button {
    width: 100%;
}

.productTitle {
    line-height: 120%;
    letter-spacing: -0.02em;
}

.nameAndPrice {
    display: flex;
    justify-content: space-between;
}

.nameAndPrice>h1:first-child {
    max-width: 350px;
}

.priceBlock {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.priceBlock>h2 {
    letter-spacing: -0.02em;
    text-decoration-line: line-through;
    font-feature-settings: 'zero' on;
    font-weight: 400;
}

.buttons {
    display: flex;
    justify-content: space-between;
}

.buttons>button {
    width: 100%;
}

.buttons>button:first-child {
    margin-right: 8px;
}

.buttons>button:last-child {
    width: 50%;
}

@media #{$bigtablet} {
    .nameAndPrice>h1:first-child {
        max-width: 275px;
    }
}

@media #{$tablet} {
    .buy_on {
        width: calc(100% - 45px);
    }
    .buttons_mt>button {
        width: 100%;
    }
}

@media #{$mobile} {
    .card {
        grid-template-columns: 1fr;
    }

    .productInfo {
        margin: 0 12px;
        position: static;
        max-width: 100%;
    }

    .buttons>button:last-child {
        width: fit-content;
        padding: 8px;
    }

    .buttons>button:last-child> :first-child {
        display: block !important;
    }
}

@media #{$mobile} {
    .nameAndPrice>h1:first-child {
        max-width: 345px;
    }
}

@media #{$small} {
    .nameAndPrice>h1:first-child {
        max-width: 235px;
    }
}