@import "styles/variables.scss";

.title {
    width: 226px;
}

.buttonBlock {
    display: flex;
    flex-direction: column;
    position: fixed;
    bottom: 40px;
    width: 310px;
}

.buttonBlock > button {
    width: 100%;
}

.buttonBlock > button:first-child {
    margin-bottom: 8px;
}

.productItem {
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
}

@media #{$tablet} {
    .title {
        width: 100%;
    }
    .buttonBlock {
        width: calc(100% - 48px);
    }
    .productItem {
        padding-bottom: 16px;
    }
    .productItem > div > div,
    .productItem > div > div > div:first-child {
        max-width: fit-content;
    }
}