@import "styles/variables.scss";

.favoritesItem {
    display: flex;
    position: relative;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    padding: 16px 0;
}

.isFullInfo {
    flex-direction: column;
}

.favoritesItem > img {
    border: 1px solid rgba(35, 35, 35, 0.3);
    background-image: url('/static/img/miniLoader.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.favoritesItem p {
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    max-width: 190px;
}

.favoritesItem:last-child {
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    margin-bottom: 100px;
}

.isBack:last-child {
    margin-bottom: 32px;
}

.close {
    position: absolute;
    top: 18px;
    right: 2px;
    cursor: pointer;
    z-index: 1;
}

.infoBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 16px;
    max-width: 212px;
    min-height: 100%;
}

.infoBlock > img {
    position: absolute!important;
    bottom: 16px;
    right: 0;
    cursor: pointer;
}

.infoBlock > div > p:first-child {
    margin-bottom: 4px;
}

.sizes > button {
    margin: 8px 4px 0 0;
    background-color: $beige;
    line-height: 32px;
    vertical-align: middle;
    border: 1px solid rgba(35, 35, 35, 0.3);
    width: fit-content;
    padding: 0 5px;
    min-width: 32px;
    height: 32px;
    cursor: pointer;
}

.ok {
    text-transform: uppercase;
    cursor: pointer;
}

.card {
    display: flex;
    position: relative;
}

.card > div.checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 8px;
    left: 8px;
    border: 1px solid $darkBlue;
    width: 20px;
    height: 20px;
    box-sizing: border-box;
}

.card > img {
    border: 1px solid rgba(35, 35, 35, 0.3);
    box-sizing: border-box;
    background-image: url('/static/img/miniLoader.svg');
    background-size: 80%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.infoBlock > div:first-child {
    max-width: 160px;
}

button.button {
    width: 100%;
    margin-top: 16px;
}

.sizes > button.activeSize {
    border: 1px solid rgba(35, 35, 35, 1);
}

p.isReturned {
    position: absolute;
    left: 8px;
    top: 65px;
    height: fit-content;
    background-color: $whiteColor;
    padding: 4px 8px;
    line-height: 140%!important;
    box-sizing: border-box;
    font-size: 12px;
    text-align: center;
    max-width: 118px;
}

@media #{$tablet} {
    .favoritesItem {
        flex-direction: column;
        padding: 16px 0 24px;
    }
    .favoritesItem:last-child {
        border-bottom: none;
    }
    .close {
        top: 16px;
        right: 0;
    }
    .sizes > button {
        font-size: 16px;
    }
    .sizes > button:last-child {
        margin-right: 0;
    }
    .ok {
        margin-top: 24px;
        font-size: 16px;
    }
    .isFullInfo .infoBlock {
        justify-content: start;
    }
    .isFullInfo .infoBlock > div:first-child {
        margin-bottom: 23px;
    }
}