@import "styles/variables.scss";

.detailsBGC {
    background: rgba(35, 35, 35, 0.8);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100001;
}

.details {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: $beige;
    z-index: 100;
    padding: 50px 24px 24px;
    min-width: 358px;
    box-sizing: border-box;
    max-height: 100vh;
    overflow-y: scroll;
}

.details * {
    color: $darkBlue;
}

.details > :first-child {
    position: absolute!important;
    top: 24px;
    right: 24px;
    cursor: pointer;
}

@media #{$tablet} {
    .details {
        left: 0;
    }
    .details p {
        font-size: 16px!important;
    }
}