@import "styles/variables.scss";

.titleRow {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    margin-bottom: 16px;
}

.total {
    display: flex;
    align-items: center;
}

.total > p {
    margin-right: 7px;
    font-size: 12px;
}

.total > div {
    display: flex;
    align-items: center;
    height: 100%;
}

.review {
    margin-bottom: 24px;
}

.review > div {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 16px;
}

.name,
.text {
    font-size: 12px;
}

.text {
    max-width: 400px;
}

.showBtn {
    display: block;
    background-color: $beige;
    text-decoration: underline;
    cursor: pointer;
}

.paginationButton {
    height: 32px;
    width: 32px;
    background-color: $beige;
    border: 1px solid $darkBlue;
    margin: 4px 4px 0 0;
    cursor: pointer;
}

.paginationButtonActive {
    opacity: 0.3;
    cursor: auto;
}

.paginationContainer {
    display: flex;
    align-items: center;
}

.paginationContainer > p {
    margin-left: 16px;
}