@import "styles/variables.scss";

.about {
    position: relative;
    z-index: 10;
}

.title {
    margin-bottom: 8px;
}
.description {
    font-size: 12px;
    margin-bottom: 24px;
    max-width: 360px;
}

.firstDescription {
    margin-bottom: 5px;
}

.infoBtn {
    display: block;
    margin-bottom: 16px;
    background-color: $beige;
    border-bottom: 1px solid $darkBlue;
    cursor: pointer;
}

.sizeItem {
    margin-bottom: 40px;
}

.name {
    margin-bottom: 16px;
}

.sizeRow {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 8px;
}

.sizeRow > p {
    font-size: 12px;
}

.titleDelivery {
    margin-bottom: 12px;
    line-height: 100%;
    text-transform: uppercase;
}

.titleDelivery:nth-last-of-type(2) {
    padding-top: 32px;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.textInfo {
    max-width: 300px;
    margin-bottom: 32px;
}

.textInfo:nth-of-type(2),
.textInfo:nth-of-type(3) {
    margin-bottom: 12px;
}

@media #{$tablet} {
    .description {
        margin-bottom: 16px;
    }
    .infoBtn {
        margin-bottom: 8px;
    }
}