@import "styles/variables.scss";

.images {
    display: flex;
    flex-wrap: wrap;
}

.images > a {
    margin-right: 4px;
}

.images .img {
    border: 1px solid rgba(35, 35, 35, 0.3);
    cursor: pointer;
    margin-top: 8px;
    width: 60px;
}

.images .img > img {
    position: relative!important;
    display: block;
    aspect-ratio: 200/289;
    background-image: url('/static/img/miniLoader.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    object-fit: cover;
}

.images .img:not(:last-child) {
    margin-right: 8px;
}