@import "styles/variables.scss";

.images {
    width: fit-content;
    padding-right: 20px;
    position: relative;
}

.images > .sale,
.imagesScrollcontainer > .sale {
    border-radius: 2px;
    border: none;
    background-color: $whiteColor;
    width: fit-content;
    padding: 2px 8px;
    position: absolute;
    z-index: 1;
    top: 16px;
    left: 16px;
    line-height: 125%;
    text-align: right;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;

}

.images > .saleValue,
.imagesScrollcontainer > .saleValue {
    left: 74px;
}

.images > div {
    border: 1px solid rgba(35, 35, 35, 0.3);
    border-bottom: none;
}

.images > div > img {
    width: 640px!important;
    height: auto!important;
    position: relative!important;
    aspect-ratio: 640 / 925;
    background-image: url('/static/img/miniLoader.svg');
    background-size: 20%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
	object-fit: cover;
}

.imagesScrollcontainer {
    width: 100%;
    overflow: hidden;
}

.imagesScrollBlock {
    display: flex;
    width: fit-content;
    transition: 0.3s;
}

.imagesScrollBlock > img {
    position: relative!important;
    width: 100%!important;
    height: auto!important;
    aspect-ratio: 640 / 925;
    background-image: url('/static/img/miniLoader.svg');
    background-size: 30%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
	object-fit: cover;
}

.mobileImages {
    position: relative;
    margin-bottom: 15px;
    z-index: 100;
}

.mobileImages > :first-child {
    position: absolute!important;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
    z-index: 10;
}

.mobileImages > div:nth-child(4) {
    position: absolute;
}

.mobileImages > div:nth-child(4) > div {
    justify-content: space-between;
    width: calc(100vw + 2px);
    height: 130px;
    padding: 0;
    transform: translateY(1px) translateX(-1px);
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

.mobileImages > :last-child {
    position: absolute!important;
    top: 50%;
    right: 12px;
    transform: translateY(-50%);
    z-index: 10;
}

.dotList {
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 40px;
    left: 12px;
}

.dotList > div {
    background-color: $darkBlue;
    margin-bottom: 6px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

@media #{$bigtablet} {
    .images {
        width: 430px;
    }
    .images > div > img {
        width: 428px!important;
    }
}

@media #{$tablet} {
    .images {
        padding: 0;
    }
    .imagesScrollcontainer > .sale {
        left: 12px;
        top: 12px;
    }
    .imagesScrollcontainer > .saleValue {
        left: 70px;
    }
}

@media #{$tablet} {
    .images {
        width: 350px;
    }
    .images > div > img {
        width: 348px!important;
    }
}
